import React from 'react'
import 'isomorphic-unfetch'

import Link from 'next/link'
import Nav from 'components/nav'
import ListJobs from 'components/listJobs'

export default class MyPage extends React.Component {
  // static async getInitialProps () {
  //   // eslint-disable-next-line no-undef
  //   const res = await fetch('http://localhost:1080/v1/jobs/all?location=')
  //   const json = await res.json()
  //   const data = { jobs_count:json.results, jobs:json.data };
  //   // console.log('data',data);
  //   return data;
  // }

  render () {
    var Jobs = [];
    this.props.url.query.data.forEach(function(job,i){
      Jobs.push(
        <div key={job._id}>
          <div>{job.text}</div>
          <sup>{job.location}</sup>
        </div>
      );
    });

    return (
        <div>
          <h2>SEARCH: {this.props.jobs_count}</h2>
          <div>
            {Jobs}
          </div>
          <Link prefetch href='/javascript'><a>&laquo; go back home</a></Link>
        </div>
    )
  }
}



